<template>
    <div class="cart">
        <h4></h4>
        <div class="cart-main">
            <div class="cart-th">
                <div class="cart-th1"></div>
                <div class="cart-th2"></div>
                <div class="cart-th3"></div>
                <div class="cart-th4"></div>
                <div class="cart-th5"></div>
                <div class="cart-th6"></div>
            </div>
            <div class="cart-body">
                <ul class="cart-list" v-for="cart in cartInfoList" :key="cart.id">
                    <li class="cart-list-con1">
                        <input
                            type="checkbox"
                            name="chk_list"
                            :checked="cart.isChecked == 1"
                            @change="updateChecked(cart, $event)"
                        />
                    </li>
                    <li class="cart-list-con2">
                        <img :src="cart.imgUrl" />
                        <div class="item-msg">{{ cart.skuName }}</div>
                    </li>
                    <li class="cart-list-con4">
                        <span class="price">{{ cart.skuPrice }}.00</span>
                    </li>
                    <li class="cart-list-con5">
                        <a href="javascript:void(0)" class="mins" @click="handler('minus', -1, cart)">-</a>
                        <input
                            autocomplete="off"
                            type="text"
                            minnum="1"
                            class="itxt"
                            :value="cart.skuNum"
                            @change="handler('change', $event.target.value * 1, cart)"
                        />
                        <a href="javascript:void(0)" class="plus" @click="handler('add', 1, cart)">+</a>
                    </li>
                    <li class="cart-list-con6">
                        <span class="sum">{{ cart.skuNum * cart.skuPrice }}</span>
                    </li>
                    <li class="cart-list-con7">
                        <a class="sindelet" @click="deleteCartById(cart)"></a>
                        <br />
                        <a href="#none"></a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="cart-tool">
            <div class="select-all">
                <input
                    class="chooseAll"
                    type="checkbox"
                    :checked="isAllCheck && cartInfoList.length > 0"
                    @change="updateAllCartChecked"
                    :disabled="cartInfoList.length == 0"
                />
                <span></span>
            </div>
            <div class="option">
                <a @click="deleteAllCheckCart"></a>
                <a href="#none"></a>
                <a href="#none"></a>
            </div>
            <div class="money-box">
                <div class="chosed">
                     <span>{{ cartCheckedNum }}</span
                    >
                </div>
                <div class="sumprice">
                    <em> </em>
                    <i class="summoney">{{ totalPrice }}</i>
                </div>
                <div class="sumbtn">
                    <a class="sum-btn" @click="$router.push('/trade')"></a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import throttle from "lodash/throttle";

export default {
    name: "ShopCart",
    mounted() {
        this.getData();
    },
    //
    methods: {
        getData() {
            this.$store.dispatch("getCartList");
        },
        //()
        handler: throttle(async function (type, disNum, cart) {
            //type:
            //disNum:+1) --1)input)
            //cart: id
            switch (type) {
                case "add":
                    //
                    disNum = 1;
                    break;
                case "minus":
                    // 1-1
                    disNum = cart.skuNum > 1 ? -1 : 0;
                    break;
                case "change":
                    //),
                    // if (isNaN(disNum) || disNum < 1) {
                    //     disNum = 1;
                    // } else {
                    //   //:
                    //   disNum = parseInt(disNum) - cart.skuNum;
                    // }
                    disNum = isNaN(disNum) || disNum < 1 ? 1 : parseInt(disNum) - cart.skuNum;
                    break;
            }
            //action
            try {
                await this.$store.dispatch("addOrUpdateShopCart", { skuId: cart.skuId, skuNum: disNum });
                //
                this.getData();
            } catch (error) {
                return error;
            }
        }, 1000),
        //
        async deleteCartById(cart) {
            try {
                //
                await this.$store.dispatch("deleteCartListBySkuId", cart.skuId);
                this.getData();
            } catch (error) {
                console.log("!");
            }
        },
        //
        async updateChecked(cart, event) {
            try {
                //
                //isChecked0|1
                let isChecked = event.target.checked ? "1" : "0";
                await this.$store.dispatch("updateCheckedById", {
                    skuId: cart.skuId,
                    isChecked,
                });
                this.getData();
            } catch (error) {
                //
                alert(error.message);
            }
        },
        //
        async deleteAllCheckCart() {
            try {
                //action
                await this.$store.dispatch("deleteAllCheckedCart");
                this.getData();
                //
            } catch (error) {
                alert(error.message);
            }
        },
        //
        async updateAllCartChecked() {
            try {
                let isChecked = event.target.checked ? "1" : 0;
                //action
                await this.$store.dispatch("updateAllCartIsChecked", isChecked);
                this.getData();
            } catch (error) {
                alert(error.message);
            }
        },
    },
    computed: {
        ...mapGetters(["cartList"]),
        //
        cartInfoList() {
            return this.cartList.cartInfoList || [];
        },
        //
        totalPrice() {
            let sum = 0;
            this.cartInfoList.forEach((item) => {
              //
                item.isChecked == 1 ? (sum += item.skuNum * item.skuPrice) : "";
            });
            //
            return sum;
        },
        //
        isAllCheck() {
            //,
            return this.cartInfoList.every((item) => item.isChecked == 1);
        },
        cartCheckedNum() {
            let PromiseAll = 0;
            this.cartInfoList.forEach((cartInfoList) => {
                let promise = cartInfoList.isChecked == 1 ? 1 : 0;
                //Promise
                PromiseAll += promise;
            });
            //
            return PromiseAll;
        },
    },
};
</script>

