<template>
    <div class="order">
        <div class="grid">
            <div>
                <h3></h3>
                <div  class="titleup">
                    <div class="titlehead">
                        : <input type="text" v-model="code">
                        : <input type="text" v-model="name">
                        : <input type="text" v-model="number">
                        <input type="button" value="" @click='add()'>
                    </div>
                </div>
                <div v-for="book in books" class="title">
                    <td>:<div>{{book.id}}</div></td>
                    <td>:<div>{{book.name}}</div></td>
                    <td>:<div>{{book.number}}</div></td>
                    <div>{{book.date | deteformat}}</div>
                    <td>:
                        <a href="#" @click='edit(book)'></a>
                        <a href="#" @click='deletebook(book)'></a>
                    </td>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    data:function() {
        return{
        code:'',
        name:'',
        number:'',
        flag:false,   //fales
        index:'',
        books:[
            {
                id:'',
                name:'',
                number:'15688888888',
            },
            {
                id:'',
                name:'',
                number:'15688888888',
            },
            {
                id:'',
                name:'',
                number:'15688888888',
            },
        ]
        }
        },
        methods:{
          //
          add(){
              // 
              if(this.code.length != 0 && this.name.length != 0 && this.number.length != 0 && !this.flag){
              var newBook = new Object();
              newBook.id = this.code;
              newBook.name = this.name;
              newBook.number = this.number;
              // newbookbooks
              this.books.push(newBook);
              // 
              this.code ='';
              this.name ='';
              this.number ='';
              }else if(this.name.length != 0){
                  this.books[this.index].name = this.name;
                  this.books[this.index].code = this.code;
                  this.books[this.index].code = this.number;
                  this.flag = false;
                  this.code ='';
                  this.name ='';
                  this.number ='';
              }
          },
          deletebook(book){
              // alert(book.name)
              //
              //
              var bookIndex = this.books.indexOf(book);
              //,
              this.books.splice(bookIndex,1);
          },
          edit(book){
              this.flag = true;
              this.code = book.id;
              this.name = book.name;
              this.number = book.number;
              this.index = this.books.indexOf(book);
          }
        }
};
</script>
<style lang="scss" scoped>
@import "../../../assets/scss/_color.scss";
@import "../../../assets/scss/_fonts.scss";
    .order{
        margin-top: 30px;
        background-color: $white-card;
        .grid{
            margin: 0 auto;
            h3{
                width: 100px;
                margin: 0 auto;
                padding-top: 15px;
            }
            .title{
            width: 500px;
            display: flex;
            flex-direction: column;
            margin-top: 15px;
            border: 1px solid $black-color;
            td{
                margin: 5px;
                display: flex;
                a{
                    margin: 0 5px;
                }
            }
        }
        .titlehead{
            width: 500px;
            display: flex;
            flex-direction: column;
            padding-top: 10px;
            input{
                margin: 5px 0;
            }
        }
        }
    }
</style>
